:root {
  --dark: #0f1c13;
  --light: #fcfaf9;
  --dark-blend: soft-light;
  --light-blend: exclusion;
  --default-font-family: "Century Gothic", sans-serif;
  --serif-font-family: "Italiana", serif;
  --size-xgg: calc(0.5rem + 15vw);
  --size-gg: calc(0.5rem + 16.18vw);
  --size-g: calc(0.5rem + 6.18vh);
  --size-m: calc(0.5rem + 3.81924vh);
  --size-p: calc(0.5rem + 1.45865941776vh);
  --size-xp: calc(1.2rem);
}
@media (min-width: 550px) {
  :root {
    --size-xp: calc(0.5rem + 1.45865941776vh);
  }
}
@media (min-width: 1024px) and (max-height: 1300px) {
  :root {
    --size-xgg: 13vw;
    --size-gg: 10vw;
    --size-g: 10vh;
    --size-m: 3.81924vw;
    --size-p: 2.36029032vw;
    --size-xp: 2.36029032vh;
  }
}
@media (min-width: 1300px) {
  :root {
    --size-xgg: 13vw;
    --size-gg: 10vw;
    --size-g: 10vh;
    --size-m: 3.81924vw;
    --size-p: 2.36029032vw;
    --size-xp: 2.36029032vh;
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "Italiana";
  src: url("../fonts/Italiana-Regular.woff2") format("woff2"), url("../fonts/Italiana-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Century Gothic";
  src: url("../fonts/CenturyGothic.woff2") format("woff2"), url("../fonts/CenturyGothic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.theme {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  transition: background-color 0.4s cubic-bezier(0.3, 1, 0.7, 1), color 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.theme.--dark {
  --primary-color: var(--dark);
  --secondary-color: var(--light);
  --blend-mode: var(--dark-blend);
}
.theme.--light {
  --primary-color: var(--light);
  --secondary-color: var(--dark);
  --blend-mode: var(--light-blend);
}
.theme ::selection {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}
.theme--border {
  border-color: var(--secondary-color);
  transition: border-color 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.theme--background {
  background-color: var(--secondary-color);
  transition: background-color 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.theme--color {
  color: var(--secondary-color);
  transition: color 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.theme--fill path {
  fill: var(--secondary-color);
  transition: fill 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.theme--cursor {
  mix-blend-mode: var(--blend-mode);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

html {
  font-size: 10px;
  font-family: var(--default-font-family);
  font-weight: 400;
  font-style: normal;
  line-height: 1.3;
  letter-spacing: 0.1rem;
  overflow-x: hidden;
}

body {
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}

/* Grainy texture animation by Geoff Graham https://css-tricks.com/snippets/css/animated-grainy-texture/ */
body:before {
  will-change: background-image;
  animation: grain 10s steps(10) infinite;
  background-image: url("../imgs/noise.png");
  content: "";
  height: 300%;
  left: -50%;
  opacity: 0.4;
  position: fixed;
  top: -100%;
  width: 300%;
  z-index: -1;
}

@keyframes grain {
  0%, 100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -9%);
  }
  20% {
    transform: translate(-13%, 5%);
  }
  30% {
    transform: translate(7%, -20%);
  }
  40% {
    transform: translate(-5%, 20%);
  }
  50% {
    transform: translate(-13%, 9%);
  }
  60% {
    transform: translate(13%, 0%);
  }
  70% {
    transform: translate(0%, 13%);
  }
  80% {
    transform: translate(3%, 25%);
  }
  90% {
    transform: translate(-9%, 9%);
  }
}
.scroller {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.container {
  position: relative;
  width: 94vw;
  height: 100%;
  margin: 0 auto;
}

.--loading .section {
  visibility: hidden;
}

.--fade {
  opacity: 0;
}